*{
  padding:0px;
  margin: 0px;
  box-sizing: border-box;
}
body{
  font-family: 'Ubuntu', sans-serif;
}
.root{
  max-width:1349px;
}
.mobile-logo{
  display: none;
}
.mobile-menu{
  display: none;
}
.header{
  width: 100%;
  height: 75px;
  background-color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  z-index: 2;
  box-shadow: 0px 0px 5px 0px #ccc;
}

.logo{
  width:223px;
  height: 130px;
}
.menu{
  display: flex;
  flex: 1;
  justify-content: space-around;
  align-items: center;
}

.menu a{
  text-decoration: none;
  color: #000;
  text-transform: uppercase;
  font-size: 15px;
  font-weight: 300;
}
.menu a:hover{
  border-bottom: 8px solid #5f259f;
  color: #5f259f;
}
.banner {
  width:100%;
  height: 500px;
  position: relative;
  top: 66px;
  background: url("./images/hp-banner1.png") no-repeat center center/cover;
  z-index:1;
}
.banner button{
  position: absolute;
    bottom: 50px;
    left: 150px;
    background-color: #5f259f;
    color: #fff;
    font-weight: bold;
    font-size: 18px;
    outline: none;
    border: 0px solid #5f259f;
    width: 200px;
    height: 45px;
    border-radius: 30px 30px;
    font-family: UniNeue,sans-serif,Helvetica,Arial;
    font-weight: 200;
    cursor: pointer;
}
.fastSecure{
  width: 100%;
  height: auto;
  margin-top: 170px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.fast-explain{
  width: 500px;
  line-height: 1.9;
}
.fast-explain div{
  padding: 12px;
  border-bottom: 1px solid #ca9090;
}
.fast-video{
  width: 550px;
  height: 280px;
}
.fast-video video{
  width:500px;
  height:400px;
}
.fast-explain .fast-h,.fast-explain .last-h{
  border-bottom:0;
}
.fast-h h1{
  color: #361374;
}
.fast-explain h2{
  color: #323232;
  font-size: 20px;
  font-weight: bold;
}
.fast-explain p{
  color: #333;
  font-size: 14px;
}
.safety{
  height: 265px;
  background: url("./images/safety.png") no-repeat center center/cover;
  display: flex;
  justify-content: space-around;
  margin-top: 50px;
  margin-bottom: 300px;
}
.safety img{
  width: 500px;
  height: 375px;
  cursor: pointer;
}
.para{
  width: 450px;
  position: relative;
  height: 400px;
}
.para p{
  position: absolute;
  bottom: 20px;
  line-height: 1.7;
  font-size: 18px;
}
.trust-banner{
  margin-top: 300px;
  width: 90%;
  height: 250px;
  background: url("./images/trust-banner.png") no-repeat center center/cover;
  display: flex;
  justify-content: space-around;
  margin: auto;
  background-color: #967edd;
  border-radius: 30px;
}
.trust-banner div{
  padding: 50px 30px;
  line-height: 1.7;
}
.pci-iso{
  display: flex;
  align-items: center;
}
.trust-banner .trust{
  height: 200px;
  margin-right: 20px;
  margin-top: 30px;
}
 .pci-iso img{
   height: 30px;
   margin-left: 30px;
}
.trust-banner h1{
  color: #fff;
}
.trust-banner p{
  color: #fff;
  font-size: 14px;
  word-spacing: 1px;
}
.partner{
  height: auto;
  padding: 180px 50px;
  background-color: #f2f2f2;
  margin-top: -117px;
  z-index: -4;
  position: relative;
}
.partner h1{
  color: #361968;
  text-align: center;
  line-height: 1.9;
}
.partner button{
    background-color: #10d6f2;
    color: #fff;
    font-weight: bold;
    font-size: 18px;
    outline: none;
    border:1px solid #10d6f2;
    width: 200px;
    height: 45px;
    border-radius: 30px 30px;
    font-family: UniNeue,sans-serif,Helvetica,Arial;
    cursor: pointer;
}
.partner i{
  margin-left: 10px;
}
.social{
  height: 320px;
  background: url("./images/partner-section-desk.png") no-repeat center center/cover;
  background-color: #f2f2f2;
}
.bussiness{
  margin: auto;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.money{
  width: 500px;
  height: 400px;
  background: url("./images/basket-mob-uc.png") no-repeat center center/cover;
}
.bussiness-details div{
  line-height: 3;
  border-bottom: 1px solid #333;
  padding-bottom: 10px;
}
.bussiness-details .last-bussiness-div{
  border-bottom: 0;
}
.bussiness-details h1{
  color: #361968;
}
.bussiness-details h2{
  color: #6739b7;
  font-weight: 200;
  font-size: 20px;
}
.bussiness-details p{
  color: #333;
}
.progress{
  height: auto;
  background-color: #f2f2f2;
  padding-bottom: 90px;
  padding-top: 50px;
}
.progress div{
  text-align: center;
  padding: 20px;
}
.progress i{
  margin-left:10px;
}
.progress div h1{
  color: #361968;
  line-height: 1.3;
  margin-bottom: 20px;
}
.all-card{
  display: grid;
  grid-template-columns: repeat(3,1fr);
  grid-gap: 50px;
  line-height: 2.2;
}
.all-card div{
  border: 1px solid #ccc;
  border-radius: 20px;
  background-color: #fff;
  height: 400px;
}
.all-card div p,h2{
  color: #4a4a4a;
  font-weight: 200;
}
.all-card .bussi-p{
  margin-top: 85px;
}
.all-card .bussi-p i{
   color: #361968;
}
.progress button{
  margin-top: 40px;
  background-color: #10d6f2;
    color: #fff;
    font-weight: bold;
    font-size: 18px;
    outline: none;
    width: 200px;
    height: 45px;
    border-radius: 30px 30px;
    font-family: UniNeue,sans-serif,Helvetica,Arial;
    border:1px solid #10d6f2;
    cursor: pointer;
}
.progress h1{
  text-align: center;
}
.footer-details{
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 30px;
  background-color: #6739b7;
}
.footer-details p,h1{
  color: #fff;
  line-height: 1.6;
}
.fb{
  height: 250px;
  background: url("./images/stats-desktop.png") no-repeat center center/cover;
  background-color: #6739b7;
}
.footer{
  display: flex;
  justify-content: space-around;
  padding: 50px 20px;
}
.footer ul{
  list-style-type: none;
}
.footer a{
  text-decoration: none;
  color: #333;
  line-height: 3;
  font-size: 13px;
}
.footer a:hover{
  color:#6739b7;
}
.footer button{
    font-weight: bold;
    font-size: 14px;
    outline: none;
    width: 150px;
    height: 45px;
    border-radius: 30px 30px;
    font-family: UniNeue,sans-serif,Helvetica,Arial;
    border: 2px solid #000;
    font-weight: bold;
    background-color: #fff;
    cursor: pointer;
}
.footer button:hover{
  border: 2px solid #6739b7;
  color: #6739b7;
}
.footer .first-a{
  font-weight: bold;
}
.footer i{
  padding:40px 10px;
  font-size: 30px;
  cursor: pointer;
}
.footer i:hover{
  color: #6739b7;
}
.footer p{
  text-align: center;
  margin-bottom: 20px;
}

@media(max-width:850px){
  .header a{
    font-size: 13px;
  }
  .all-card{
    grid-template-columns: 1fr 1fr;
  }
  .safety{
     flex-direction: column;
     justify-content: space-around;
     align-items: center;
  }
  
  .safety-img{
    width: 300px;
    height: 200px;

  }
  
  .social{
    height: 170px;
  }
  .bussiness-details{
    padding: 40px;
  }
}
@media (max-width: 500px) {
  .header{
    width: 100%;
    height: 65px;
    display: block;
    background-color: #fff;
    position: fixed;
    z-index: 2;
    border-bottom: 1px solid #ccc;
    box-shadow: 0px 0px 5px 0px #ccc;
  }
  .logo{
    display: none;
  }
  .menu{
    display: none;
  }
  .mobile-logo{
    height: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0px 12px;
  }
  .mobile-logo i{
    font-size: 30px;
    color: #5f259f;
    cursor: pointer;
  }
  .mobile-logo div{
    flex:1;
    text-align: center;
  }
  .mobile-logo .mlogo{
    width: 160px;
    height: 110px;
  }
  .mobile-menu{
    display: block;
    height: 100%;
    background-color: #fff;
    transform: translateX(-1000px);
    transition: all 0.5s;
  }
  .mobile-menu a{
    text-decoration: none;
    color: #333;
    line-height: 2;
    font-size: 1rem;
  }
  .mobile-menu a:hover{
    color: #5f259f;
  }
  .banner{
    width: 100%;
  }
  .open{
    height: 100vh;
    margin-top: -6px;
    transform: translateX(0px);
   }
   .open div{
     padding: 10px;
   }
   .mobile-menu button{
    background-color: #fff;
    color: #000;
    border-radius: 50%;
    float: right;
    margin: 10px;
    width: 30px;
    height: 30px;
    font-size: 20px;
    border: 1px solid #fff;
   }
  .banner button{
    margin-left: -60px;
  }
  .fastSecure{
    flex-direction: column-reverse;
    margin-top: 100px;
  }
  .fast-explain{
    width: 365px;
    padding: 10px;
  }
  .para p{
    padding: 10px;
  }
  .fast-video{
    width: 300px;
    height: 350px;
  }
  .fast-video video{
    width:300px;
    height:350px;
  }
  .safety {
    height: auto;
    flex-direction: column;
    margin-top: 50px;
    margin-bottom: 20px;
    }

    .safety img {
      width: 100%;
      height: 250px;
      padding: 0px 10px;
    }
    .para {
      width: 100%;
      height: 200px;
      padding: 10px;
    }
    .trust-banner{
      width: 95%;
      flex-direction: column;
      height: 100%;
      margin-top: 60px;
    }
    .trust-banner .trust {
      height: 100px;
    width: 100px;
    margin: auto;
    margin-top: 30px;
  }
  .pci-iso{
    flex-direction: row;
    margin-left: -65px;
  }
  .pci-iso img{
    margin-top: 20px;
  }
 .partner h1{
   line-height: 1.5;
 }
  .social {
    width: 100%;
    height: 87px;
   }
   .bussiness{
    flex-direction: column-reverse;
    margin-top: 50px;
   }

   .money{
     width: 100%;
   }
   .bussiness-details{
     padding: 10px;
   }
   .bussiness-details i{
     margin-left:20px;
   }
   .all-card{
     grid-template-columns: 1fr;
   }
   .footer-details{
     flex-direction: column;
     justify-content:space-around;
     align-items: center;
     padding: 70px 0px;
   }
   .footer-details div{
     margin-top: 65px;
   }
   .fb{
     width: 100%;
   }
   .footer{
     display:grid;
     grid-template-columns: repeat(2,1fr);
   }
   .footer li a{
       padding: 10px;
   }
}